.go-top-mod a 
{
    width: 45px;
    height: 45px;
    display: block;
    position: fixed;
    right: 20px;
    bottom: 20px;
    // line-height: 18px;
    // padding: 5px 5px 12px 11px;
    // background: #4B4949;
    text-decoration: none;
    color: #fff;
    // border-radius: 5px;
    background: url(../../../images/go-top-45x45.png?__sprite) ;
    width: 45px;
    height:45px;
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -o-transition: all 0.2s;
    -ms-transition: all 0.2s;
    transition: all 0.2s;

    &:hover{
        background: #3A3939;
        background: url(../../../images/go-top-45x45-hover.png?__sprite) ;
    }
    
}